.media-list-item {
  margin-top: 10px;
  .media-body, .media-right, .media-left {
    vertical-align: middle;
    padding-left: 10px;
    padding-right: 10px;
  }
  .media {
    padding-bottom: 15px;
    margin-top: 0;
    margin-bottom: 15px;
    border-bottom: 1px solid $light-grey;
    @include opacity(1);
    @include transition($transition-base);
    &.remove {
      @include opacity(0.4);
      .remove-item {
        display: none;
      }
    }
  }

}
