@mixin button-variant-inline($color, $background, $border) {

  @if $background == transparent {
    $background: #FFF;
  }

  color: $color;
  background-color: $background;
  border-color: $border;

  &:focus,
  &.focus {
    color: $background;
    background-color: darken($color, 10%);
    border-color: darken($color, 25%);
  }
  &:hover {
    color: $background;
    background-color: darken($color, 10%);
    border-color: darken($color, 12%);
  }
  &:active,
  &.active,
  .open > &.dropdown-toggle {
    color: $background;
    background-color: darken($color, 10%);
    border-color: darken($color, 12%);

    &:hover,
    &:focus,
    &.focus {
      color: $background;
      background-color: darken($color, 17%);
      border-color: darken($color, 25%);
    }
  }
  &:active,
  &.active,
  .open > &.dropdown-toggle {
    background-image: none;
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus,
    &.focus {
      background-color: $color;
      border-color: $color;
    }
  }

  .badge {
    color: $color;
    background-color: $background;
  }
}

@import "hover/hover";

.btn-detail {
  font-weight: normal;
  @include shutter-out-horizontal();
}

.btn-default2 {
  @extend .btn-default;
  @include gradient-vertical(#FBFBFB, #DDDDDD, 0%, 100%);
}

.btn-primary-bg {
  @include button-variant(#FFF, $btn-primary-color, darken($btn-primary-color, 10%));
}

.btn-wth {
  @include button-variant(#FFF, #F54A59, darken(#F54A59, 10%));
}

.btn-wth-default {
  @include button-variant(#F54A59, #FFF, $btn-default-border);
}

.btn-primary-inline {
  @include button-variant-inline(#F54A59, transparent, transparent);
}

.btn-default-inline {
  @include button-variant-inline($btn-default-color, transparent, transparent);
  &:focus,
  &.focus {
    color: $btn-default-color;
    background-color: $line;
    border-color: $line;
  }
  &:hover {
    color: $btn-default-color;
    background-color: $line;
    border-color: $line;
  }
  &:active,
  &.active,
  .open > &.dropdown-toggle {
    color: $btn-default-color;
    background-color: $line;
    border-color: $line;

    &:hover,
    &:focus,
    &.focus {
      color: $btn-default-color;
      background-color: $line;
      border-color: $line;
    }
  }
}

.btn-min {
  min-width: 106px;
  text-align: center;
}

.btn-min-sm {
  min-width: 160px;
  text-align: center;
}

.btn {

  text-transform: uppercase;
  min-width: 34px;

  &,
  &:active,
  &.active {
    &:focus,
    &.focus {
      outline: 0 none;
    }
  }

  &:hover,
  &:focus,
  &.focus {
    outline: 0 none;
  }

  &:active,
  &.active {
    outline: 0 none;
  }
  font-weight: bold;
}

.btn-circle {
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  width: 34px;
  padding-left: 0;
  padding-right: 0;
  text-align: center;
}
