.photo-box-detail {

  $photo-detail-info-width: 470px;

  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(#000, 0.85);
  z-index: -1;
  visibility: hidden;
  &.active {
    z-index: 1002;
    visibility: visible;
  }
  .photo-detail-top {
    padding: 15px;
    color: #ffffff;
    line-height: 34px;
    @include clearfix;
    .name {
      float: left;
      margin-right: 300px;
    }
    .action {
      margin: 0;
      padding: 0;
      list-style-type: none;
      &.navbar-left {
        li {
          margin-right: 15px;
          padding-right: 15px;
          border-right: 1px solid $light-slate;
        }
      }
      &.navbar-right {
        > li {
          margin-left: 15px;
        }
      }
      > li {
        float: left;
        font-size: 21px;

        > a {
          color: #ffffff;
          display: inline-block;
          float: left;
          width: 34px;
          height: 34px;
          text-align: center;
          line-height: 32px;
          -webkit-border-radius: 3px;
          -moz-border-radius: 3px;
          border-radius: 3px;
          @include transition($transition-base);
          &:hover {
            background-color: $light-slate;
            color: #ffffff;
          }
        }
      }
    }
  }
  .photo-detail-content {
    @include flexbox;
    @include align-items(flex-start);
    .photo-detail-img {
      @include flex(1);
      height: calc(100vh - 65px);
      position: relative;
      padding: 10%;
      figure {
        @include center();
        max-width: 100%;
        max-height: 100%;
      }
      img {
        width: 100%;
        height: auto;
      }
      .photo-detail-img-control {
        @include center(false, true);
        width: 60px;
        height: 60px;
        padding: 5px 0;
        text-align: center;
        background-color: rgba(#A4A4A4, 0.4);
        cursor: pointer;
        @include transition($transition-base);
        &:hover {
          background-color: rgba(#A4A4A4, 1);
        }
        i {
          font-size: 50px;
          line-height: 0.95;
          color: #ffffff;
        }
        &.img-control-prev {
          left: 15px;
        }
        &.img-control-next {
          right: 15px;
        }
      }
    }
  }
  &.active-info {
    padding-right: $photo-detail-info-width;
    .photo-detail-info {
      right: 0;
    }
  }
  .photo-detail-info {
    width: $photo-detail-info-width;
    right: -$photo-detail-info-width;
    @include transition($transition-base);
    //height: calc(100vh - 65px);
    height: 100vh;
    overflow: auto;
    background-color: #ffffff;
    padding: 15px;
    position: absolute;
    top: 0;
    word-break: break-all;
    h3 {
      margin: -15px -15px 15px;
      padding: 15px;
      @include clearfix;
      border-bottom: 1px solid $light-slate;
      a {
        float: right;
      }
    }
    h4 {
      font-size: 14px;
      font-weight: normal;
      margin: 0 0 20px;
      @include clearfix;
      a {
        float: right;
      }
    }
    hr {
      margin-left: -15px;
      margin-right: -15px;
    }
    .tags-text {
      span {
        color: #9b9b9b;
        display: inline-block;
        margin-right: 10px;
      }
    }
    dl {
      &.dl-horizontal {
        dt {
          width: 80px;
        }
        dd {
          margin-left: 100px;
        }
      }
      dt {
        margin-bottom: 10px;
      }
      dd {
        ul {
          @include clearfix;
          li {
            float: left;
            margin: 0 5px 5px 0;
          }
        }
      }
    }
  }
}
